import React, { Component } from "react";
// import { Redirect } from 'react-router-dom'
import "../component/myStyles.css";
// import axios from 'axios';
 import {storage} from '../firebase'

export default class Order extends Component {
  constructor() {
    super();

    this.state = {
      value: 0,
      redirect: null,
      selectedFile: null
    };
  };

  decrease = (e) => {
    e.preventDefault();
    this.setState({ value: (e.target.value = this.state.value -= 1) });
    console.log(this.state.value);
  };

  increase = (e) => {
    e.preventDefault();
    this.setState({ value: (e.target.value = this.state.value += 1) });
    console.log(this.state.value);
  };

  fileSelectorHandler = (event) => {
    this.setState({ selectedFile: event.target.files[0] })
  };

  fileUploadHandler = () => {
      const uploadTask = storage.ref(`uploads/${this.state.selectedFile.name}`).put(this.state.selectedFile);
      uploadTask.on(
          'state_changed',
          snapshot =>{},
          error => {
              console.log(error);
          },
          ()=> {
              storage
                .ref('uploads')
                .child(this.selectedFile.name)
                .getDownloadURL()
                .then(url => {
                    console.log(url)
                });

          }
      )
  };

  render() {
    return (
      <div className="col-sm-8 offset-sm-2">
        <h1 className="item-name"> Order: ITEM NAME</h1>
        <form>
          <h6 className="category">CATEGORY:</h6>
          <p className="description">This is my item's description</p>
          <select className="form-select" aria-label="Default select example">
            <option selected>Color</option>
            <option value="1">Gray</option>
            <option value="2">Blue</option>
            <option value="3">White</option>
          </select>
          <br />
          <select className="form-select" aria-label="Default select example">
            <option selected>Size</option>
            <option value="1">XS</option>
            <option value="2">S</option>
            <option value="3">M</option>
            <option value="3">L</option>
            <option value="3">XL</option>
            <option value="3">XXL</option>
            <option value="3">3XL</option>
          </select>
          <br />
          <p className="price container">
            PRICE: $ <span className="item-price">30</span>
          </p>
          <div className="input-group">
            <input
              type="file"
              onChange={this.fileSelectorHandler}
              className="form-control"
              id="inputGroupFile04"
              aria-describedby="inputGroupFileAddon04"
              aria-label="Upload"
            />
            <button
              onClick={this.fileUploadHandler}
              className="btn btn-outline-secondary"
              type="button"
              id="inputGroupFileAddon04"
            >
              Button
            </button>
          </div>
          <br />
          <div className="def-number-input number-input">
            <button
              onClick={(e) => this.decrease(e)}
              className="minus"
            ></button>
            <input
              className="quantity"
              name="quantity"
              value={this.state.value}
              onChange={(e) => console.log("change")}
              type="number"
            />
            <button onClick={(e) => this.increase(e)} className="plus"></button>
          </div>
          <br />
          <button type="submit" className="btn btn-outline-info">
            Submit
          </button>
        </form>
      </div>
    );
  }
}
